.wines{
    display:flex;
    justify-content: center;
    align-items: center;
    padding-top:10vh;
    width:100%;
    flex-direction: column;
    margin:auto;
}

.wines h1{
    padding: 20px;
}
.grid-container{
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(350px,1fr));
    width:80%;
    justify-items: center;
    align-self: center;
    align-items: center;
}
.grid-container div{
    position: relative;
    display:flex;
    align-items: center;
    justify-content: end;
    margin: 20px;
    flex-direction: column;
    background-image: url(./slika-boca.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size:contain;
    width: 300px;
    height: 300px;
    overflow:hidden;
    box-shadow: 5px 5px 15px 0px rgba(128, 128, 128,0.6);
    transition:all 200ms ease-in;
    cursor:pointer;
    

  
}

.grid-container p{
    position: absolute;
    bottom:0;
    background-color:rgba(0, 0, 0, 0.7);
    width:100%;
    text-align: center;
    height: 60px;
    margin: 0;
    padding-top: 20px;
    font-size: 20px;
    opacity:0;
    transform: translateY(100%);
    transition:all 200ms ease-in-out;
    color:white;

}
.grid-container div:hover{
    transform: scale(1.1);
    box-shadow: 5px 5px 25px 0px rgba(128, 128, 128,0.6);
}
.grid-container div:hover>p{
    transform: translateY(0%);
    opacity: 100;
}
.wine1,.wine2,.wine3,.wine4,.wine5,.wine6{
    opacity: 0;
    
}
.wine1{
    animation: bottle-animation 0.5s ease-in forwards;
}
.wine2{
    animation: bottle-animation 0.3s ease-in forwards 200ms;
}
.wine3{
    animation: bottle-animation 0.3s ease-in forwards 300ms;
}
.wine4{
    animation: bottle-animation 0.3s ease-in forwards 400ms;
}
.wine5{
    animation: bottle-animation 0.3s ease-in forwards 500ms;
}
.wine6{
    animation: bottle-animation 0.3s ease-in forwards 600ms;
}
@keyframes bottle-animation {
    0%{ opacity: 0; }
    100%{ opacity:100;}
    
}

@media only screen and (max-width: 880px) {
    .grid-container{
        width:100%;
    }
}