.contact{
    width: 80%;
    display: flex;
    flex-direction: row;
     align-items: center;
     justify-content: center;
    margin-top:50px;
    padding: 0 10vw;


    min-height: 90vh;

}
.contact h1{
    padding:50px;
}
.contact-container{
    width: 50%;
    display: flex;
    flex-direction: column;
    animation: info-animation 1s ease-in-out forwards;


}
.contact-info{
    position:relative;
    padding: 20px 0;
    display: flex;
    align-items: center;
}
.contact-info .icon {
    min-width: 60px;
    height:60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: rgb(202, 202, 202);

}
.contact-info  .icon img{
    width: 30px;
    height: 30px;
}

.contact-info  .text{
    display: flex;
    flex-direction: column;
    margin:0 0 0 20px;
    font-size:16px;
    font-weight: 300;
    color: white;
    padding: 0;
    
}


.contact-info  .text h3{
    font-weight: 700 ;
    margin:0;
    padding: 0;
    font-size: 20px;
    color:rgb(194, 61, 63)
}
.contact-info  .text p{
    margin:0;
    padding: 0;
    color: gray;
    font-weight: 500;
    line-height: 20px;

}

.maps-widget{
    width:40%; 
    display:flex;
    
    border-radius: 5px;
    min-width: 35px;
    animation:map-animation 1s ease-in-out forwards;
    
}
.maps-widget iframe{
    box-shadow: 5px 5px 10px 0px rgb(128, 125, 125);
}

@media only screen and (max-width: 880px) {
    
    .contact{
        flex-wrap: wrap;
        width: 100%;
        padding:0;
        
    }
    .maps-widget{
        width: 100%;
        margin: 10%;
        padding:0;
    }
    .contact-info{
        justify-content: flex-start;

    }
    .contact-container{
        width: 100%;
        padding:10%;
    }
}
@keyframes map-animation{
    0%{opacity: 0;scale: 1;}
    50%{opacity: 100;scale:1.1;}
    100%{opacity: 100;scale: 1;}
    
}
@keyframes info-animation{
    0%{transform: translateX(-100%); opacity: 0;}
    20%{transform: translateX(-50%); opacity: 0;}
    
    60%{transform: translateX(15%); opacity: 100;}
    
    80%{transform: translateX(-5%); opacity: 100;}
    
    90%{transform: translateX(2%);opacity: 100;}
    100%{
        transform: translateX(0%); opacity: 100;
    }
    
}