.modal{
    z-index:50;
    position: fixed;
    top:0vh;
    flex-direction: row;
    margin: 100px  auto;
    animation-fill-mode: forwards;
    animation: modal-animation 300ms ease-in;
    display:block;
   

   
    
}
.modal .info{
    background-color: white;
    display: grid;
    grid-template-columns: repeat(auto-fit,minmax(300px,1fr));
    width:80%;
    align-self: center;
    align-items: center;
    margin:auto;

}
.modal .info .info-text{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    flex-direction: column;
    padding: 50px;
}
.modal .info img{
    width:100%;
    
}

@keyframes modal-animation {
    0%{opacity:0; scale: 0.5;}
    
    100%{opacity:100; scale:1;}

}

.background{
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0,0.6);
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 49;
}
#close{
    position:absolute;
    padding: 10px;
    margin: 0;
    line-height: 30px;
    top:0%;
    right: 10%;
    font-size: 40px;
    border-radius: 50%;
    border:none;
    background-color: transparent;
    transition: scale 300ms ease-in-out;

}
#close:hover{
    scale:1.2;

}

@media only screen and (max-width: 880px) {
    .modal{
        position: fixed;
        width: 100%;
        height: 100vh;
        overflow-y: scroll;
    }
    .modal .info{
        width:100%;
    }
}