/* No CSS *//*# sourceMappingURL=style-nav.css.map */
header{
    background:rgb(0, 0, 0,1);
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; 
            position: fixed;
            width:100%;
            color: white;
            top:0;
            z-index: 100;
            
            
  }
  #logo{
    float:left;
    padding: 10px 0;
  }
  nav{
    float:right;
    overflow-x: hidden;
  }
  
  header::after{
    content:'';
    display:table;
    clear:both;
   }
  
  #navigation{
    width:80%;
    height: 80px;
    margin:0 auto;
  }
  nav ul{
    margin:0;
    padding:0;
    list-style:none;
    
  }
  nav li{
    display:inline-block;
    margin-left:50px;
    margin-top:15px;
    padding:10px 0;
    margin-bottom:15px;
    position:relative;
  }
  nav a {
    color: white;
    text-decoration:none;
    text-transform:uppercase;
    font-size:16px;
    position: relative;
    transition: all 200ms ease-in;
  }
  nav a:hover{
    color: rgb(175, 166, 166);
  }
  nav a:focus{
    outline:none;
}
nav a:active{
  color: black;
  
}
  nav a::before{
    content:"";
    display:block;
    position:absolute;
    height:5px;
    border-radius:50;
    background-color:rgb(126, 25, 27);
    top:-10px;
    width:0%;
    transition:width 200ms ease-in-out;
    z-index: 100;
  }  
  nav a:hover::before{
    width:100%;
  }
  .burger{
    position:absolute;
   right:5%;
   top:30px;
   display: none;
   cursor: pointer;
   
  }
  .burger div{
    height:3px;
    width:25px;
    background-color: white;
    margin-top: 5px;
    transition: all 200ms ease-in-out;
  
  }
  

  @media only screen and (max-width: 880px) {
    .burger{
      display:block;
    }
    nav{
      position: absolute;
      top:10vh;
      padding-top:10vh;
      right:0;
      background-color: rgb(0, 0, 0,0.5);
      flex-direction: column;
      flex-wrap: nowrap;
      width:50%;
      height:90.5vh;
      transform: translateX(100%);
    }
    nav li{
      display: block;
      margin-bottom: 20vh;
      opacity:0;
      transition: all 500ms ease-in;
      color:white;
    }
  }

   .transition{
    transition: all 300ms ease-in;
  }

  .activated{
    transform: translateX(0%);
    opacity:1
  }
  .clicked #line1{
    animation:ani1 400ms ease-in-out;
    animation-fill-mode: forwards;
   }

   
   .clicked #line2{
     opacity:0;

  }
  
  .clicked #line3{
    animation:ani2 400ms ease-in-out;
    animation-fill-mode: forwards;
    
  }

  .closed #line1{
    animation: closed1 400ms ease-in-out;
    animation-fill-mode: backwards;
  }
  .closed #line3{
    animation: closed2 400ms ease-in-out;
    animation-fill-mode: backwards;
  }

    @keyframes ani1{
      0%{transform: translateY(0px) rotate(0deg);}
      50%{transform: translateY(8px) rotate(0deg);}
      100%{transform: translateY(8px) rotate(45deg);}
    }
    @keyframes ani2{
      0%{transform: translateY(0px) rotate(0deg);}
      50%{transform: translateY(-8px) rotate(0deg);}
      100%{transform: translateY(-8px) rotate(-45deg);}
    }

    
  @keyframes closed2{
    100%{transform: translateY(0px) rotate(0deg);}
    50%{transform: translateY(-8px) rotate(0deg);}
    0%{transform: translateY(-8px) rotate(45deg);}
  }
  @keyframes closed1{
    100%{transform: translateY(0px) rotate(0deg);}
    50%{transform: translateY(8px) rotate(0deg);}
    0%{transform: translateY(8px) rotate(-45deg);}
  }