html{
    scroll-snap-type: y proximity;
    overflow-y: overlay;
    scroll-behavior: smooth;
    overflow-x: hidden;
    font-family:"Montserrat",sans-serif;
}
#logo-img{
    opacity: 0;
    animation: logo-animation 200ms ease-in forwards;
    
}

.home{
    display: block;
    width:100%;
    margin:0;
    padding: 0;
    
   
    
}


.home .landing{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    background-image: url(./pexels-freestocksorg-839462.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    background-size: cover;
    justify-content: flex-start;
    
}
.home .landing img{
    min-width:300px;
    width:400px;
    padding-top: 100px;
}

.home section{
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    scroll-snap-align: start;
    
    
}
.home section article{
    min-width:400px;
    width:600px;
    max-width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    transition: opacity 200ms ease-in;
    
    
}
.home section article img,
.home section article .text {
    width:50%;
    

}



.about-wine{
    color: white;
    background-color: black;

}

.about-vineyard{
    width: 100%;
    height: 100vh;
    background-image: url(./pexels-pixabay-39511.jpg);
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center; 
    background-size: cover;

}
.about-vineyard .text{
    font-size: 18px;
    color: white;
    font-weight: 500;
    background-color: rgba(0, 0, 0, 0.3);
    padding:50px;
    border-radius: 5px;
    
}
.home h2{
    color:rgb(194, 61, 63)
}

@keyframes logo-animation {
    0%{opacity: 0;}
    100%{opacity:100;}
    
}
@media only screen and (max-width: 880px) {
    html{
        scroll-snap-type: y mandatory;
    }
    
}